
.panel {
  height: 115vh;
  position: sticky;
  top: 0;
  display: flex;
  justify-content: center;
  text-align: center;
  scroll-behavior:smooth;
}


.section {
  width: 100%;
  height: 100vh;
}

h1{
  font-size: 5rem;
}

.flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.column {
  flex-direction: column;
  gap: 1rem;
}

.boxes-container {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 1rem;
}

.purple {
  height: 100vh;
  min-height: 100vh;
}


/* PANEL 1 */
.panel.one h1{
  margin-top: 0;
}

.panel.one p{
  margin-top: 0;
}

.panel.one img{
  max-width: 15%;
  border-radius: 10px;
  align-self: center;
  padding: 5px;
}


.panel.one{
  flex-direction: column;
}


/* PANEL FÃ„RGER */

.panel.one {
  background-color: #F5DD90;
}
.panel.two {
  background-color: #9BD1E5;
}
.panel.three {
  background-color: #AFB3F7;
}

.panel.four {
  background-color: #43C59E;
}


/* SCROLL DOWN "KNAPP"*/
.scroll-down {
  position: absolute;
  bottom: 200px;
  left: 50%;
  transform: translate(-50%, 0);
  color: black;
  text-transform: uppercase;
  font-size: 1rem;
  overflow: visible;
}

.scroll-down .arrow {
  position: relative;
  top: -4px;
  margin: 0 auto;
  width: 20px;
  height: 20px;
  background-image: url(data:image/svg+xml;base64,);
  background-size: contain;
}

/* Mobilanpassning */

@media only screen and (max-width: 1000px) {
  h1{
    font-size: 2rem;
  }
  .panel.one img{
    max-width: 30%;
  }
}